body {
  font-family: 'Poiret One', sans-serif;
  background: #fff;
  height: 100vh;
  margin:0;
  padding:0;
}

.titleText {
  color:rgb(241, 163, 166);
}

.boxIcon {
  color:rgb(248, 147, 80);
  padding-left:10px;
}
.titleBox {
  color:#FDAB73;
  padding-right:10px;
  border-top-right-radius:5px;
  border-bottom-right-radius:5px;
}

.weatherContainer {
  text-align:center;
  width:250px;
  margin:auto;
  padding:10px;
  background: #fff;
}
.errorMessage {
  color:red;
  font-size:200%;
}

.temperatureP {
  color:#444;
  font-weight:100;
  font-size:400%;
  text-align:center;
  padding:0;
  margin:0;
}
.temperatureP::after {
  content: "Â°";
  font-weight:100;
}
.tempSymbol {
  font-weight:100;
  margin:0;
  padding:0;
}
.description {
  color:#444;
  font-size:200%;
  margin:0;
  padding:0;
  font-weight:800;
}

.locationP {
  text-transform: uppercase;
  color:#444;
  text-align: center;
  font-size: 200%;
  font-weight:100;
}

#weatherIcon {
  color:#444;
  width:100%;
  text-align:center;
  font-size:300%;
  padding:20px 0;
}


